import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import SelectAjax from '../common/Input/SelectAjax';

export default class YearSemesterInput extends Component {
  state = {};

  componentWillReceiveProps = () => {};
  render() {
    const { fieldYear = 'yearEduId', fieldSemester = 'semesterId' } = this.props;
    const { labelYear = 'Năm học', labelSemester = 'Học kì' } = this.props;
    const { disabled, optionFieldDecorator = {}, span = 6, semesterSpan } = this.props;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const yearEduId = getFieldValue(fieldYear);
    return (
      <Fragment>
        <Col xs={24} sm={12} md={span}>
          <Form.Item label={labelYear}>
            {getFieldDecorator(
              fieldYear,
              optionFieldDecorator.yearEduId
            )(
              <SelectAjax
                disabled={!!disabled}
                service={getYearEducations}
                setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
        <Col xs={24} sm={12} md={semesterSpan || span}>
          <Form.Item label={labelSemester}>
            {getFieldDecorator(
              fieldSemester,
              optionFieldDecorator.semesterId
            )(
              <SelectAjax
                disabled={!!disabled}
                setFieldsValue={setFieldsValue}
                service={getSemesters}
                params={{ yearEduId }}
                allowGetData={!!yearEduId}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
      </Fragment>
    );
  }
}



