import React,  { Fragment } from 'react';
import { Input, Row, message } from 'antd';
import { Link } from 'react-router-dom';
import useFormModal from 'components/common/FormModal/useFormModal';

import { requiredRule } from '../../../utils/validateRules';
import CrudPage from '../../../components/common/CrudPage';
import {
  deleteTrainingScoreQuestion,
  duplicateTrainingScoreQuestion,
  getRootTrainingScoreQuestion,
  updateRootTrainingScoreQuestion,
} from '../../../services/studentPortal';
import YearSemesterInput from '../../../components/Eem/YearSemesterInput';
import YearSemesterInputV2 from '../../../components/Eem/YearSemesterInput_v2';
import Btn from '../../../components/common/Btn/Btn';
import InputOnlyNumber from '../../../react-uikit/component/input/InputOnlyNumber';

const FormItemsDuplicate = props => {
  return (
    <div>
      <div
        style={{
          fontSize: '14px',
          fontWeight: 'bold',
          backgroundColor: '#70c2ff21',
          color: '#096dd9',
          padding: '5px',
        }}
      >
        Từ:
      </div>
      <Row>
        <YearSemesterInput
          {...props}
          optionFieldDecorator={{
            yearEduId: {
              rules: [requiredRule],
            },
            semesterId: {
              rules: [requiredRule],
            },
          }}
          span={24}
        />
      </Row>
      <div
        style={{
          fontSize: '14px',
          fontWeight: 'bold',
          backgroundColor: '#70c2ff21',
          color: '#096dd9',
          padding: '5px',
        }}
      >
        Đến:
      </div>
      <Row>
        <YearSemesterInputV2
          {...props}
          fieldYear="nextYearEduId"
          fieldSemester="nextSemesterId"
          // labelYear="nextYearEduId"
          // fieldSemester="nextSemesterId"
          optionFieldDecorator={{
            yearEduId: {
              rules: [requiredRule],
            },
            semesterId: {
              rules: [requiredRule],
            },
          }}
          span={24}
        />
      </Row>
    </div>
  );
};

export default () => {
  const handleDuplicate = async values => {
    await duplicateTrainingScoreQuestion(values);
    message.success('Sao chép thành công');
  };
  const FormModalDuplicate = useFormModal({
    title: 'Sao chép',
    FormItems: FormItemsDuplicate,
    onSubmit: handleDuplicate,
  });
  const model = [
    { field: 'id', name: 'Id' },
    { field: 'label', name: 'Tên', rules: [requiredRule], formItem: <Input.TextArea autoSize /> },
    {
      field: 'maxGrade',
      name: 'Điểm tối đa',
      rules: [requiredRule],
      formItem: <InputOnlyNumber />,
    },
    { field: 'yearEduId', name: 'Năm học', rules: [requiredRule], formItem: <Input /> },
    { field: 'yearEdu', name: 'Năm học' },
    { field: 'semesterId', name: 'Học kỳ', rules: [requiredRule], formItem: <Input /> },
    { field: 'semester', name: 'Học kỳ' },
    { field: 'order', name: 'Vị trí', rules: [requiredRule], formItem: <Input /> },
  ];

  const config = {
    title: 'Quản lý câu hỏi rèn luyện',
    serviceGetList: getRootTrainingScoreQuestion,
    serviceCreate: updateRootTrainingScoreQuestion,
    serviceUpdate: updateRootTrainingScoreQuestion,
    serviceDelete: deleteTrainingScoreQuestion,
    searchFormItems: [YearSemesterInput],
    tableColumns: ['yearEdu', 'semester', 'label', 'maxGrade', 'order'],
    tableExtraActions: ({ record }) => {
      console.log(record);
      return (
        <Link
          to={{
            pathname: `/admin/quan-ly-chi-tiet-cau-hoi-ren-luyen`,
            search: `?id=${record.id}`,
          }}
        >
          <Btn primary>Chi tiết</Btn>
        </Link>
      );
    },
    addExtraActions: () => {
      return (
        <Fragment>
          <Btn
            primary
            onClick={() =>
              FormModalDuplicate.show({
                initValue: {},
              })
            }
          >
            Sao chép
          </Btn>
          <FormModalDuplicate.Modal />
        </Fragment>
      );
    },
    saveFormItems: [
      props => (
        <Row>
          <YearSemesterInput
            {...props}
            optionFieldDecorator={{
              yearEduId: {
                rules: [requiredRule],
              },
              semesterId: {
                rules: [requiredRule],
              },
            }}
            span={24}
          />
        </Row>
      ),
      'label',
      'maxGrade',
      'order',
    ],
  };
  return (
    <div>
      <CrudPage model={model} config={config} />
    </div>
  );
};



